const PrivacyState = {
  PUBLIC: "公开",
  PRIVATE: "隐藏"
}
const PictureLifeState = {
  EXIST: "正常",
  DISAPPEAR: "不存在"
}
const SizeType = {
  TRANSVERSE: "横图",
  VERTICAL: "竖图",
  SQUARE: "方图"
}

export default {
  PrivacyState,
  PictureLifeState,
  SizeType,
  equal(key, enumType, enumValue) {
    return enumType[key] === enumValue
  },
  getKey(enumType, enumValue) {
    for (let keys of Object.keys(enumType)) {
      if (enumType[keys] === enumValue) {
        return keys
      }
    }
  },
  toArray(enumType) {
    let array = []
    for (let key in enumType) {
      array.push({
        value: key,
        label: enumType[key]
      })
    }
    return array
  }
}
