import { MenuEnum, OptionEnum } from "../model"
import {
  mdiAccount,
  mdiPictureInPictureTopRightOutline,
  mdiSettings
} from "@mdi/js"
export function roleOptionListConstant() {
  return [
    new OptionEnum("经销商", 1),
    new OptionEnum("跟单员", 2),
    new OptionEnum("管理员", 3)
  ]
}

export function menuListConstant() {
  return [
    new MenuEnum("用户管理", "/user", mdiAccount),
    new MenuEnum("图片管理", "/picture", mdiPictureInPictureTopRightOutline),
    new MenuEnum("操作码", "/authorizeCode", mdiSettings)
  ]
}

export const NOOP = () => {}
